// æŽ¥æ”¶ä¸€ä¸ªè™šæ‹ŸDOMèŠ‚ç‚¹ï¼Œå¹¶ä¸”å°†è™šæ‹ŸDOMèŠ‚ç‚¹æ¸²æŸ“æˆçœŸå®žçš„DOMèŠ‚ç‚¹ï¼Œç„¶åŽæ·»åŠ åˆ°containerå®¹å™¨ä¸­è¿›è¡ŒæŒ‚è½½
function render(vnode, container) {
  if (vnode.isReactComponent) {
    container.innerHTML = '';
    // å¦‚æžœæ˜¯ç›´æŽ¥æ¸²æŸ“ç±»ç»„ä»¶
    const component = vnode;
    component._container = container;
    vnode = vnode.render(); // ç›´æŽ¥è°ƒç”¨ç»„ä»¶å¯¹è±¡çš„render()æ–¹æ³•æ‹¿åˆ°å¯¹åº”çš„è™šæ‹ŸDOMå¼€å§‹æ¸²æŸ“
  }

  if (typeof vnode.tag === 'function' && !vnode.isReactComponent) {
    // è¿™æ˜¯ä¸€ä¸ªå‡½æ•°ç»„ä»¶
    if (vnode.tag.prototype && vnode.tag.prototype.render) {
      // è¿™æ˜¯ä¸€ä¸ªç±»ç»„ä»¶
      const component = new vnode.tag(vnode.attrs);
      console.dir(component);
      component._container = container; // æ ¹æ®ç±»ç»„ä»¶ååˆ›å»ºç»„ä»¶å®žä¾‹å¹¶è°ƒç”¨render()å‡½æ•°æ‹¿åˆ°å¯¹åº”çš„è™šæ‹ŸDOMèŠ‚ç‚¹
      vnode = component.render();
    } else {
      vnode = vnode.tag(vnode.attrs || {}); // æ‰§è¡Œå‡½æ•°ç»„ä»¶ï¼Œå¹¶ä¼ å…¥å±žæ€§é›†ï¼Œæ‹¿åˆ°å¯¹åº”çš„è™šæ‹ŸDOMèŠ‚ç‚¹å³å¯
    }
  }

  // ç”±äºŽReactDOMå¯ä»¥ç›´æŽ¥æ¸²æŸ“ä¸€æ®µæ–‡æœ¬ï¼Œæ‰€ä»¥è¿™ä¸ªvnodeå¯èƒ½æ˜¯ä¸€ä¸ªå­—ç¬¦ä¸²æˆ–æ•°å­—
  if (typeof vnode === 'string' || typeof vnode === 'number') {
    const textNode = document.createTextNode(vnode); // ç›´æŽ¥åˆ›å»ºä¸€ä¸ªæ–‡æœ¬èŠ‚ç‚¹
    container.appendChild(textNode); // å°†åˆ›å»ºçš„æ–‡æœ¬èŠ‚ç‚¹æ·»åŠ åˆ°å®¹å™¨ä¸­å³å¯
    return;
  }

  // å¦‚æžœvnodeä¸æ˜¯ä¸€ä¸ªå­—ç¬¦ä¸²ï¼Œé‚£ä¹ˆå…¶å°±æ˜¯ä¸€ä¸ªè™šæ‹ŸDOMå¯¹è±¡ï¼Œé‚£ä¹ˆæˆ‘ä»¬æ ¹æ®tagååˆ›å»ºå¯¹åº”çš„çœŸå®žDOMå…ƒç´ å³å¯
  const element = document.createElement(vnode.tag);
  // å¤„ç†å±žæ€§
  Object.keys(vnode.attrs).forEach((key) => {
    if (key === 'className') {
      key = 'class';
    }
    const value = vnode.attrs[key];
    if (typeof value === 'function') {
      // å¦‚æžœå±žæ€§å€¼ä¸ºä¸€ä¸ªå‡½æ•°ï¼Œè¯´æ˜Žæ˜¯ç»‘å®šäº‹ä»¶
      element[key.toLowerCase()] = value;
    } else {
      element.setAttribute(key, vnode.attrs[key]);
    }
  });
  // å¤„ç†å­èŠ‚ç‚¹ï¼Œé€’å½’æ¸²æŸ“å­èŠ‚ç‚¹
  vnode.children.forEach((child) => {
    return render(child, element);
  });
  container.appendChild(element); // å°†æ¸²æŸ“å¥½çš„è™šæ‹ŸDOMèŠ‚ç‚¹æ·»åŠ åˆ°å®¹å™¨ä¸­
}
export default render;
